import { createSlice } from "@reduxjs/toolkit";
const initialState = {
    totalCount: 0,
    totalPrice: 0,
    items: [],
};
const cartSlice = createSlice({
    name: "cartSlice",
    initialState,
    reducers: {
        addItems: (state, { payload }) => {
            state.totalCount = payload.totalCount;
            state.totalPrice = payload.totalPrice;
            state.items = payload.items;
        },
        addItem: (state, { payload }) => {
            const findItem = state.items.find((item) => item.id === payload.id);
            if (findItem) {
                findItem.count += 1;
            }
            else {
                state.items.push(Object.assign(Object.assign({}, payload), { count: 1 }));
            }
            state.totalCount += 1;
            state.totalPrice += Number(payload.price);
            const json = JSON.stringify(state);
            localStorage.setItem("cart", json);
        },
        addManyItem: (state, { payload }) => {
            const findItem = state.items.find((item) => item.id === payload.item.id);
            if (findItem) {
                findItem.count += payload.count;
            }
            else {
                state.items.push(Object.assign(Object.assign({}, payload.item), { count: payload.count }));
            }
            state.totalCount += payload.count;
            state.totalPrice += Number(payload.item.price) * payload.count;
            const json = JSON.stringify(state);
            localStorage.setItem("cart", json);
        },
        removeItem: (state, { payload }) => {
            const findItem = state.items.find((item) => item.id === payload.id);
            state.items = state.items.filter(({ id }) => id !== payload.id);
            state.totalCount -= findItem.count;
            state.totalPrice -= Number(payload.price) * findItem.count;
            const json = JSON.stringify(state);
            localStorage.setItem("cart", json);
        },
        removeItems(state) {
            state.items = [];
            state.totalPrice = 0;
            state.totalCount = 0;
            localStorage.removeItem("cart");
        },
        minusItem: (state, { payload }) => {
            const findItem = state.items.find((item) => item.id === payload.id);
            if (findItem.count === 1) {
                state.items = state.items.filter(({ id }) => id !== payload.id);
            }
            else {
                findItem.count -= 1;
            }
            state.totalCount -= 1;
            state.totalPrice -= Number(payload.price);
            const json = JSON.stringify(state);
            localStorage.setItem("cart", json);
        },
    },
});
export const { actions } = cartSlice;
export default cartSlice.reducer;
