import { createSlice } from "@reduxjs/toolkit";
const initialState = {
    name: "",
    brand: [],
    manufacturer: [],
    categories: [],
    minPrice: 0,
    maxPrice: 10000,
};
const filtersSlice = createSlice({
    name: "filtersSlice",
    initialState,
    reducers: {
        changeProp: (state, { payload }) => {
            for (let [key, value] of Object.entries(payload)) {
                state[key] = value;
            }
        },
        addType: (state, { payload }) => {
            state.categories.push(payload);
        },
        removeType: (state, { payload }) => {
            state.categories = state.categories.filter((type) => type !== payload);
        },
        changeMinPrice: (state, { payload }) => {
            state.minPrice = payload;
        },
        changeMaxPrice: (state, { payload }) => {
            state.maxPrice = payload;
        },
    },
});
export const { actions } = filtersSlice;
export default filtersSlice.reducer;
