const sortMethods = {
    name_asc: (items) => items.sort((a, b) => (a.name > b.name ? 1 : -1)),
    name_desc: (items) => items.sort((a, b) => (b.name > a.name ? 1 : -1)),
    price_asc: (items) => items.sort((a, b) => Number(a.price) - Number(b.price)),
    price_desc: (items) => items.sort((a, b) => Number(b.price) - Number(a.price)),
};
const useSortMethod = (key, items) => {
    return sortMethods[key]([...items]);
};
export default useSortMethod;
