import { createSlice } from "@reduxjs/toolkit";
const initialState = {
    allGoods: [],
    currentGoods: [],
    pages: 1,
    currentPage: 1,
    limit: 9,
    sortMethod: "name_asc",
};
const goodsSlice = createSlice({
    name: "goodsSlice",
    initialState,
    reducers: {
        addAllItems: (state, { payload }) => {
            state.allGoods = payload.items;
        },
        addCurrentItems: (state) => {
            state.currentGoods = state.allGoods.slice((state.currentPage - 1) * state.limit, state.currentPage * state.limit);
            state.pages = Math.ceil(state.allGoods.length / state.limit);
        },
        changeCurrentPage: (state, { payload }) => {
            state.currentPage = payload;
            state.currentGoods = state.allGoods.slice((state.currentPage - 1) * state.limit, state.currentPage * state.limit);
        },
        changeSortMethod: (state, { payload }) => {
            state.sortMethod = payload;
        },
    },
});
export const { actions } = goodsSlice;
export default goodsSlice.reducer;
